<template>
  <div class="container--vertical">
    <router-link tag="a" to="/admin" title="В админку">
      <ui-button icon="home"></ui-button>
    </router-link>

    <h1>Редактировать игру {{ editedGame.title }}</h1>
    <form class="form" @submit.prevent="changeGame(editedGame['.key'])">
      <ui-textbox class="form__input" floating-label label="Название" v-model="editedGame.title"></ui-textbox>
      <ui-textbox
        class="form__input"
        floating-label
        multi-line
        label="Описание"
        v-model="editedGame.description"
      ></ui-textbox>
      <ui-select
        label="Жанр"
        placeholder="Выбрать жанр"
        v-model="editedGame.genre"
        :options="genres"
      ></ui-select>
      <ui-select
        label="Платформа"
        placeholder="Выбрать плафторму"
        v-model="editedGame.platform"
        :options="platforms"
      ></ui-select>
      <ui-textbox
        v-model="editedGame.video"
        class="form__input"
        floating-label
        label="Ссылка на YouTube"
      ></ui-textbox>
      <ui-textbox
        v-model="editedGame.gif"
        class="form__input"
        floating-label
        label="Ссылка на ролик"
      ></ui-textbox>
      <div class="form__switches">
        <ui-switch v-model="editedGame.isChild">Для детей</ui-switch>
        <ui-switch
          v-model="editedGame.isLocalMultiplayer"
          v-show="editedGame.platform == 'PS4'.toLowerCase()"
        >На двоих</ui-switch>
      </div>
      <div v-for="(tag, index) in editedGame.tags" :key="index" class="form__tags">
        <ui-textbox v-model="tag.value" class="form__input form__input--tag" label="Тег"></ui-textbox>
        <ui-button class="button button--form" raised @click.prevent="deleteTag(index)">
          <ui-icon icon="delete"></ui-icon>
        </ui-button>
      </div>
      <ui-button
        class="button button--form button--addtag"
        raised
        v-show="editedGame.tags.length <= 1"
        @click.prevent="addTag"
      >Добавить тег</ui-button>
      <div class="form__images">
        <img :src="image" :alt="editedGame.title" v-for="image in images" :key="image.url" />
      </div>
      <ui-fileupload color="primary" name="file" @change="uploadImages" multiple>Выберите файлы</ui-fileupload>
      <ui-button
        class="button button--form button--savegame"
        raised
        :disabled="!isDisable"
      >Изменить игру</ui-button>
    </form>
    <ui-alert
      @dismiss="closeAlert"
      type="success"
      removeIcon
      v-show="showAlertChangeGame"
    >Данные игры изменены</ui-alert>
  </div>
</template>

<script>
import { storage } from "../firebase";

export default {
  name: "Edit",
  data() {
    return {
      showAlertChangeGame: false,
      editedGame: {},
      files: null,
      images: [],
      genres: [
        "симулятор",
        "шутер",
        "экшн",
        "музыкальная",
        "песочница",
        "файтинг",
        "хоррор",
        "спорт",
        "квест",
        "стратегия",
        "головоломка",
        "аттракцион",
        "приключение",
        "творчество",
        "многопользовательская",
        "гонка",
        "аркада",
        "интерактивное кино"
      ],
      platforms: ["htc", "psvr", "ps4"]
    };
  },
  methods: {
    changeGame(gameKey) {
      this.$store.dispatch("editGame", [gameKey, this.editedGame]);

      if (this.files !== null) {
        for (let file of this.files) {
          storage
            .ref(
              "games/" +
                this.editedGame.title
                  .split(" ")
                  .join("_")
                  .toLowerCase() +
                "/" +
                file.name
            )
            .put(file);
        }
      }

      this.showAlertChangeGame = true;
      setTimeout(() => {
        this.$router.push("/admin");
      }, 1500);
    },
    uploadImages(files) {
      this.files = files;
    },
    addTag() {
      this.editedGame.tags.push({ value: "" });
    },
    deleteTag(index) {
      this.editedGame.tags.splice(index, 1);
    },
    closeAlert() {
      this.showAlertChangeGame = true;
    }
  },
  created() {
    this.editedGame = this.$store.getters.showGames.find(game => {
      return game.id == this.$route.params.id;
    });
  },
  mounted() {
    storage
      .ref(
        "games/" +
          this.editedGame.title
            .split(" ")
            .join("_")
            .toLowerCase()
      )
      .listAll()
      .then(snap => {
        snap.items.forEach(itemRef => {
          itemRef.getDownloadURL().then(imgUrl => {
            this.images.push(imgUrl);
            if (imgUrl.includes("thumbnail")) {
              this.images.pop(imgUrl);
            }
          });
        });
      });
  },
  computed: {
    isDisable() {
      return (
        this.editedGame.title != "" &&
        this.editedGame.description != "" &&
        this.editedGame.genre != "все" &&
        this.editedGame.video != "" &&
        this.editedGame.tags.length != 0 &&
        this.editedGame.image1 != "" &&
        this.editedGame.image2 != "" &&
        this.editedGame.image3 != "" &&
        this.editedGame.thumbnail != ""
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.form {
  position: relative;

  &__images {
    display: flex;
    margin-bottom: 10px;
    margin-left: -5px;

    img {
      display: block;
      width: calc(33.3333% - 5px);
      margin-left: 5px;
      border-radius: 4px;
    }
  }
}
</style>