<template>
  <div class="game-card" @mouseover="showVideo" @mouseleave="hideVideo" :class="{expand: isShowVideo}">
    <div class="game-card__media">
      <div class="game-card__video" v-if="isShowVideo">
        <!-- <ui-progress-circular :auto-stroke="false" :size="54" v-show="!isLoadedVideo"></ui-progress-circular> -->
        <p class="game-card__not-video" v-if="!game.gif">Видео пока нет ¯\_(ツ)_/¯</p>
        <video playsinline autoplay muted loop :src="game.gif" ref="gameVideo"></video>
      </div>
      <div class="game-card__image">
        <img class="game-card__img" :src="game.thumbnail" :alt="game.titletitle" />
        <ui-icon icon="play_circle_outline"></ui-icon>
      </div>
    </div>

    <div class="game-card__info">
      <h2 class="game-card__title">{{ game.title }}</h2>
      <p class="game-card__desc">{{ game.description }}</p>
      <div class="game-card__bottom">
        <span class="game-card__genre">{{ game.genre }}</span>
        <div v-if="game.tags" class="game-card__tags">
          <span
            class="game-card__tag"
            v-for="(tag, index) in game.tags"
            :key="index"
          >{{ tag.value + " " }}</span>
        </div>
        <span v-if="game.isMultiplayer" class="game-card__mp">на двоих</span>
      </div>
      <router-link
        tag="div"
        :to="{ name : 'GameDetail', params: {id: game.id}}"
        @click.native="hideVideo"
        v-on:click.native="$emit('change-game', game)"
      >
        <ui-button class="button--viewgame" v-show="isShowVideo">
          Перейти к игре
          <ui-icon>keyboard_arrow_right</ui-icon>
        </ui-button>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "game-card",
  props: [
    "game"
  ],
  data() {
    return {
      isShowVideo: false,
      isLoadedVideo: false
    };
  },
  methods: {
    showVideo() {
      this.isShowVideo = true;

      // this.$refs.gameVideo.onloadstart = () => {
      //   this.isLoadedVideo = true;
      // };
    },
    hideVideo() {
      this.isShowVideo = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/utils/variables.scss";
</style>