import Vue from "vue";
import Vuex from "vuex";
import { vuexfireMutations, firebaseAction } from "vuexfire";
import { db } from "../firebase";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);
// Vue.use(firebase);

function checkTags(arr1, arr2) {
  if (arr1 && arr2) {
    if (arr1[0] != undefined && arr1[1] != undefined && arr2[0] != undefined && arr2[1] != undefined) {
      if (arr1[0].value == arr2[0].value || arr1[1].value == arr2[1].value) {
        return true;
      } else {
        return false;
      }
    } else if (arr1[0] != undefined && arr1[1] != undefined && arr2[0] != undefined) {
      if (arr1[0].value == arr2[0].value || arr1[1].value == arr2[0].value) {
        return true;
      } else {
        return false;
      }
    } else if (arr1[0] != undefined && arr2[0] != undefined && arr2[1] != undefined) {
      if (arr1[0].value == arr2[0].value || arr1[0].value == arr2[1].value) {
        return true;
      } else {
        return false;
      }
    } else if (arr1[0] != undefined && arr2[0] != undefined) {
      if (arr1[0].value == arr2[0].value) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  } else {
    return false;
  }
}

export default new Vuex.Store({
  namespaced: true,
  state: {
    games: [],
    wishlistIds: [],
  },
  plugins: [createPersistedState({})],
  getters: {
    showGames: (state) => {
      return state.games;
    },
    showGamesByQuery: (state) => (query) => {
      return state.games.filter((game) => {
        return game.title
          .toString()
          .toLowerCase()
          .includes(query.toString().toLowerCase());
      });
    },
    showGamesByPlatform: (state) => (
      platform,
      genre,
      query,
      isChild,
      isLocalMultiplayer
    ) => {
      if (platform == "PS4") {
        if (isChild && isLocalMultiplayer && genre == "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase()) &&
              game.isChild &&
              game.isLocalMultiplayer
            );
          });
        } else if (isChild && isLocalMultiplayer && genre !== "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase()) &&
              game.isChild &&
              game.isLocalMultiplayer &&
              game.genre == genre
            );
          });
        } else if (isChild && genre == "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase()) &&
              game.isChild
            );
          });
        } else if (isChild && genre !== "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase()) &&
              game.isChild &&
              game.genre == genre
            );
          });
        } else if (isLocalMultiplayer && genre == "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase()) &&
              game.isLocalMultiplayer
            );
          });
        } else if (isLocalMultiplayer && genre !== "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase()) &&
              game.isLocalMultiplayer &&
              game.genre == genre
            );
          });
        } else if (genre !== "все") {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.genre == genre &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase())
            );
          });
        } else {
          return state.games.filter((game) => {
            return (
              game.platform == "PS4".toString().toLowerCase() &&
              game.title
                .toString()
                .toLowerCase()
                .includes(query.toString().toLowerCase())
            );
          });
        }
      } else if (isChild && genre == "все") {
        return state.games.filter((game) => {
          return (
            game.platform == platform.toString().toLowerCase() &&
            game.title
              .toString()
              .toLowerCase()
              .includes(query.toString().toLowerCase()) &&
            game.isChild
          );
        });
      } else if (isChild && genre !== "все") {
        return state.games.filter((game) => {
          return (
            game.platform == platform.toString().toLowerCase() &&
            game.title
              .toString()
              .toLowerCase()
              .includes(query.toString().toLowerCase()) &&
            game.isChild &&
            game.genre == genre
          );
        });
      } else if (genre !== "все") {
        return state.games.filter((game) => {
          return (
            game.platform == platform.toString().toLowerCase() &&
            game.genre == genre &&
            game.title
              .toString()
              .toLowerCase()
              .includes(query.toString().toLowerCase()) &&
            game.genre == genre
          );
        });
      } else {
        return state.games.filter((game) => {
          return (
            game.platform == platform.toString().toLowerCase() &&
            game.title
              .toString()
              .toLowerCase()
              .includes(query.toString().toLowerCase())
          );
        });
      }
    },
    showSimilarGames: (state) => (id, genre, tags, platform) => {
      return state.games.filter((game) => {
        return (
          (game.id != id &&
            game.genre == genre &&
            checkTags(tags, game.tags) &&
            game.platform == platform) ||
          (game.id != id &&
            checkTags(tags, game.tags) &&
            game.platform == platform)
        );
      });
    },
    wishlist: (state) => (query) => {
      return state.games.filter((game) => {
        return (
          state.wishlistIds.includes(game.id) &&
          game.title
            .toString()
            .toLowerCase()
            .includes(query.toString().toLowerCase())
        );
      });
    },
    showLikedGames: (state) => {
      return state.wishlist.filter((game) => {
        return game.title.toString().toLowerCase();
      });
    },
    getGameById: (state) => (id) => {
      return state.games.find((game) => {
        return game.id === id;
      });
    },
  },
  // showLikedGames: (state) => (query) => {
  //   return state.wishlist.filter((game) => {
  //     return game.title
  //       .toString()
  //       .toLowerCase()
  //       .includes(query.toString().toLowerCase());
  //   });
  // },
  // wishlist: (state) => (query) => {
  //   return state.games.filter((game) => {
  //     return (
  //       state.wishlistIds.includes(game.id) &&
  //       game.title
  //         .toString()
  //         .toLowerCase()
  //         .includes(query.toString().toLowerCase())
  //     );
  //   });
  // },
  // getGameById: (state) => (id) => {
  //   return state.games.find((game) => {
  //     return game.id === id;
  //   });
  // },
  // showBackgrounds: (state) => (theme) => {
  //   if (theme === "все") {
  //     return state.backgrounds;
  //   } else {
  //     return state.backgrounds.filter((back) => {
  //       return back.theme === theme;
  //     });
  //   }
  // },
  actions: {
    bindGames: firebaseAction(({ bindFirebaseRef }) => {
      return bindFirebaseRef("games", db.ref("games"));
    }),
    addGame: firebaseAction(
      (
        context,
        [
          gameId,
          gameTitle,
          gameDesc,
          gameGenre,
          gamePlatform,
          gameImage1,
          gameImage2,
          gameImage3,
          gameThumbnail,
          gameTags,
          gameVideo,
          gameGif,
          isChild,
          isLocalMultiplayer,
        ]
      ) => {
        return db.ref("games").push({
          id: gameId,
          title: gameTitle,
          description: gameDesc,
          genre: gameGenre,
          platform: gamePlatform,
          image1: gameImage1,
          image2: gameImage2,
          image3: gameImage3,
          thumbnail: gameThumbnail,
          tags: gameTags,
          video: gameVideo,
          gif: gameGif,
          isChild: isChild,
          isLocalMultiplayer: isLocalMultiplayer,
        });
      }
    ),
    removeGame: firebaseAction((context, gameKey) => {
      db.ref("games/" + gameKey).remove();
    }),
    editGame: firebaseAction((context, [gameKey, editedGame]) => {
      db.ref("games/" + gameKey).update({
        title: editedGame.title,
        description: editedGame.description,
        genre: editedGame.genre,
        platform: editedGame.platform,
        video: editedGame.video,
        gif: editedGame.gif,
        tags: editedGame.tags,
        isChild: editedGame.isChild,
        isLocalMultiplayer: editedGame.isLocalMultiplayer,
      });
    }),
  },
  mutations: {
    // SET_GAMES(state, games) {
    //   state.games = games;
    // },
    ...vuexfireMutations,
    addGame(state, gameId) {
      if (!state.wishlistIds.includes(gameId)) {
        state.wishlistIds.push(gameId);
      }
    },
    removeGame(state, gameId) {
      const index = state.wishlistIds.indexOf(gameId);
      if (index !== undefined) {
        state.wishlistIds.splice(index, 1);
      }
    },

    // addGame(state, gameId) {
    //   if (!state.wishlistIds.includes(gameId)) {
    //     state.wishlistIds.push(gameId);
    //   }
    // },
    // removeGame(state, gameId) {
    //   const index = state.wishlistIds.indexOf(gameId);
    //   if (index !== undefined) {
    //     state.wishlistIds.splice(index, 1);
    //   }
    // },
  },
});
