<template>
  <div class="container--vertical">
    <ui-tabs fullwidth>
      <ui-tab title="Добавить игру">
        <form class="form" ref="addGameForm" @submit.prevent="pushGame()">
          <ui-textbox
            v-model="gameTitle"
            class="form__input"
            floating-label
            label="Название"
            :required="true"
          ></ui-textbox>
          <ui-textbox
            v-model="gameDesc"
            class="form__input"
            floating-label
            multi-line
            label="Описание"
            :required="true"
          ></ui-textbox>
          <ui-select label="Жанр" placeholder="Выбрать жанр" :options="genres" v-model="gameGenre"></ui-select>
          <ui-select
            label="Платформа"
            placeholder="Выбрать плафторму"
            :options="platforms"
            v-model="gamePlatform"
          ></ui-select>
          <ui-textbox
            v-model="gameGif"
            class="form__input"
            floating-label
            label="Ссылка на ролик"
          ></ui-textbox>
          <ui-textbox
            v-model="gameVideo"
            class="form__input"
            floating-label
            label="Ссылка на YouTube"
            :required="true"
          ></ui-textbox>
          <div class="form__switches">
            <ui-switch v-model="isChild">Для детей</ui-switch>
            <ui-switch
              v-model="isLocalMultiplayer"
              v-show="gamePlatform == 'PS4'.toLowerCase()"
            >На двоих</ui-switch>
          </div>
          <div v-for="(tag, index) in gameTags" :key="index" class="form__tags">
            <ui-textbox v-model="tag.value" class="form__input form__input--tag" label="Тег"></ui-textbox>
            <ui-button class="button button--form" raised @click.prevent="deleteTag(index)">
              <ui-icon icon="delete"></ui-icon>
            </ui-button>
          </div>
          <ui-button
            class="button button--form button--addtag"
            raised
            v-show="gameTags.length <= 1"
            @click.prevent="addTag"
          >Добавить тег</ui-button>
          <ui-fileupload
            ref="inputFiles"
            color="primary"
            name="file"
            @change="uploadImages"
            multiple
            :required="true"
          >Выберите файлы</ui-fileupload>
          <ui-button
            class="button button--form button--savegame"
            raised
            :disabled="!isDisable"
          >Сохранить игру</ui-button>
        </form>
      </ui-tab>

      <ui-tab title="Изменить/удалить игру" class="ui-tab-list">
        <table class="table">
          <thead>
            <tr class="table__row table__row--head">
              <th class="table__cell table__cell--header">
                <input type="text" placeholder="Название" v-model="query" class="table__input" />
              </th>
              <th class="table__cell table__cell--header table__cell--header-platform">Платформа</th>
              <th class="table__cell table__cell--header table__cell--header-actions">Действия</th>
            </tr>
          </thead>
          <tbody>
            <tr class="table__row" v-for="(game, index) in showAllGames" :key="index">
              <td class="table__cell table__cell--title">{{ game.title }}</td>
              <td class="table__cell table__cell--platform">{{ game.platform }}</td>
              <td class="table__cell table__cell--actions">
                <router-link :to="{ name: 'edit', params: {id: game.id} }">
                  <ui-button
                    class="table__button table__button--change"
                    color="orange"
                    icon="create"
                    title="Изменить"
                  ></ui-button>
                </router-link>
                <ui-button
                  class="table__button"
                  icon="delete"
                  title="Удалить"
                  @click="deleteGame(game['.key'])"
                ></ui-button>
              </td>
            </tr>
          </tbody>
        </table>
      </ui-tab>
    </ui-tabs>
    <ui-alert
      @dismiss="closeAlert"
      type="success"
      removeIcon
      v-show="showAlertAddGame"
    >Игра добавлена в каталог</ui-alert>
    <ui-alert
      @dismiss="closeAlert"
      type="success"
      removeIcon
      v-show="showAlertDeleteGame"
    >Игра удалена</ui-alert>
  </div>
</template>

<script>
import { db } from "../firebase";
import { storage } from "../firebase";

export default {
  name: "Admin",
  data() {
    return {
      // isDisable: true,
      showAlertAddGame: false,
      showAlertDeleteGame: false,
      query: "",
      gameId: 0,
      gameTitle: "",
      gameDesc: "",
      gameGenre: "все",
      gamePlatform: "htc",
      gameImage1: "",
      gameImage2: "",
      gameImage3: "",
      gameThumbnail: "",
      gameTags: [],
      gameVideo: "https://www.youtube.com/embed/код видео",
      gameGif: "",
      isChild: false,
      isLocalMultiplayer: false,
      genres: [
        "симулятор",
        "шутер",
        "экшн",
        "музыкальная",
        "песочница",
        "файтинг",
        "хоррор",
        "спорт",
        "квест",
        "стратегия",
        "головоломка",
        "аттракцион",
        "приключение",
        "творчество",
        "многопользовательская",
        "гонка",
        "аркада",
        "интерактивное кино"
      ],
      platforms: ["htc", "psvr", "ps4"],
      files: null
    };
  },
  methods: {
    pushGame() {
      this.$store.dispatch("addGame", [
        this.gameId,
        this.gameTitle,
        this.gameDesc,
        this.gameGenre,
        this.gamePlatform,
        this.gameImage1,
        this.gameImage2,
        this.gameImage3,
        this.gameThumbnail,
        this.gameTags,
        this.gameVideo,
        this.gameGif,
        this.isChild,
        this.isLocalMultiplayer
      ]);
      this.showAlertAddGame = true;

      setTimeout(() => {
        location.reload();
      }, 1500);
    },
    uploadImages(files) {
      this.files = files;
      let tasks = [];
      let task;
      for (let file of this.files) {
        task = storage
          .ref(
            "games/" +
              this.gameTitle
                .split(" ")
                .join("_")
                .toLowerCase() +
              "/" +
              file.name
          )
          .put(file);
        tasks.push(task);
      }

      tasks.forEach(task => {
        task.on(
          "state_changed",
          () => {},
          () => {},
          snapshot => {
            task.snapshot.ref.getDownloadURL().then(downloadURL => {
              if (downloadURL.includes("1.jpg")) {
                this.gameImage1 = downloadURL;
              } else if (downloadURL.includes("2.jpg")) {
                this.gameImage2 = downloadURL;
              } else if (downloadURL.includes("3.jpg")) {
                this.gameImage3 = downloadURL;
              } else {
                this.gameThumbnail = downloadURL;
              }
              console.log(snapshot);
            });
          }
        );
      });
    },
    deleteGame(gameKey) {
      this.$store.dispatch("removeGame", gameKey);
      this.showAlertDeleteGame = true;
      setTimeout(() => (this.showAlertDeleteGame = false), 2000);
    },
    addTag() {
      this.gameTags.push({ value: "" });
    },
    deleteTag(index) {
      this.gameTags.splice(index, 1);
    },
    closeAlert() {
      this.showAlertAddGame = false;
    }
  },
  mounted() {
    console.log(this.gameTags);
    this.$store.dispatch("bindGames");
    let lastGameId;
    db.ref("games")
      .limitToLast(1)
      .once("value")
      .then(snapshot => {
        snapshot.forEach(childSnap => {
          lastGameId = childSnap.val().id;
          this.gameId = lastGameId + 1;
        });
      });
  },
  computed: {
    showAllGames() {
      return this.$store.getters.showGamesByQuery(this.query);
    },
    isDisable() {
      return (
        this.gameTitle != "" &&
        this.gameDesc != "" &&
        this.gameGenre != "все" &&
        this.gameVideo != "" &&
        this.gameTags.length != 0 &&
        this.gameImage1 != "" &&
        this.gameImage2 != "" &&
        this.gameImage3 != "" &&
        this.gameThumbnail != ""
      )
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/utils/variables.scss";
@import "@/assets/scss/utils/layout.scss";

.table {
  width: 100%;
}

.ui-tab-list {
  padding: 0;
}
</style>