<template>
  <div class="container--vertical">
    <router-link tag="a" to="/" title="На главную">
      <ui-button icon="home"></ui-button>
    </router-link>
    <h1 class="about-header">Забронируйте место</h1>
    <form
      action="https://send.pageclip.co/15aM5PlDtw71Dc6mPeLGw0SuDRDWXyQj"
      method="post"
      class="form-reserve pageclip-form"
      autocomplete="off"
      ref="formReserve"
    >
      <input
        type="text"
        class="input"
        id="name"
        name="Имя"
        value
        placeholder="Имя"
        v-model="valueName"
        title
        required
      />
      <input
        type="tel"
        class="input"
        id="tel"
        name="Телефон"
        :value="valueTel"
        v-imask="maskTel"
        @focus="showMask"
        @blur="hideMask"
        @accept="onAccept"
        placeholder="Телефон в формате 8-(XXX)-XXX-XX-XX"
        pattern="8-\([0-9]{3}\)-[0-9]{3}-[0-9]{2}-[0-9]{2}"
        title
        required
      />
      <div class="datetime">
        <input
          type="text"
          id="date"
          name="Дата"
          class="input"
          :value="valueDate"
          v-imask="maskDate"
          @focus="showMask"
          @blur="hideMask"
          @accept="onAccept"
          placeholder="Дата в формате ДД.ММ"
          pattern="\d{2}.\d{2}"
          title
          required
        />
        <input
          type="text"
          id="time"
          name="Время"
          class="input"
          :value="valueTime"
          v-imask="maskTime"
          @focus="showMask"
          @blur="hideMask"
          @accept="onAccept"
          placeholder="Время в формате ЧЧ:ММ"
          title
          pattern="\d{2}:\d{2}"
          required
        />
      </div>
      <input
        type="number"
        id="count"
        name="Количество человек"
        class="input"
        placeholder="Количество человек (максимум - 5)"
        v-model="playerCount"
        min="1"
        max="5"
        title
        required
      />
      <ui-radio-group name="Платформа" v-model="valuePlatform" :options="optionsPlatform">Платформа</ui-radio-group>
      <!-- <ui-checkbox-group name="Платформа[valuePlatform]" :options="optionsPlatform" v-model="valuePlatform">Платформа</ui-checkbox-group> -->
      <textarea
        name="Дополнительно"
        rows="5"
        placeholder="Дополнительные пожелания"
        class="input textarea"
        title
      ></textarea>
      <button
        :disabled="!isComplete"
        class="button pageclip-form__submit"
        :class="{disabled: !isComplete}"
        type="submit"
      >
        <span>Забронировать</span>
        <ui-ripple-ink></ui-ripple-ink>
      </button>
      <div class="pageclip-form__error hidden" ref="errorMessage">
        <span>Запрос отклонён, попробуйте снова</span>
        <a class="back-home button-close" href="#" @click.prevent="closeErrorMessage">Закрыть</a>
      </div>
    </form>
  </div>
</template>

<script>
import { IMaskDirective } from "vue-imask";

export default {
  name: "Reserve",
  data() {
    return {
      isFormSend: false,
      valueContainerDate: "",
      valueContainerTime: "",
      valueName: "",
      valueTel: "",
      maskTel: {
        mask: "{8}-(000)-000-00-00",
        lazy: true
      },
      valueDate: "",
      maskDate: {
        mask: "00.00",
        lazy: true
      },
      valueTime: "",
      maskTime: {
        mask: "00:00",
        lazy: true
      },
      playerCount: "",
      valuePlatform: "HTC",
      optionsPlatform: [
        {
          label: "HTC",
          value: "HTC"
        },
        {
          label: "PSVR",
          value: "PSVR"
        },
        {
          label: "PS4",
          value: "PS4"
        }
      ]
    };
  },
  directives: {
    imask: IMaskDirective
  },
  mounted() {
    const errorMessage = document.querySelector(".pageclip-form__error hidden");

    Pageclip.form(this.$refs.formReserve, {
      onSubmit: (event) => {

      },
      onResponse: (error, response) => {
        if (error) {
          this.$refs.errorMessage.classList.remove("hidden");
        }
      },
      successTemplate:
        "<span>Спасибо, заявка успешно отправлена</span><a class='back-home' href='/'>Вернуться на главную</a><button class='form-close'></button>"
    });
  },
  methods: {
    showMask(evt) {
      switch (evt.target.id) {
        case "tel":
          this.maskTel.lazy = false;
          break;
        case "date":
          this.maskDate.lazy = false;
          break;
        case "time":
          this.maskTime.lazy = false;
          break;
      }
    },
    hideMask(evt) {
      switch (evt.target.id) {
        case "tel":
          this.maskTel.lazy = true;
          if (this.valueTel.length <= 1) {
            this.$nextTick(() => (this.valueTel = ""));
          } else {
            this.$nextTick(() => (this.maskTel.lazy = false));
          }
          break;
        case "date":
          this.maskDate.lazy = true;
          if (this.valueContainerDate.length > 0)
            this.$nextTick(() => (this.maskDate.lazy = false));
          break;
        case "time":
          this.maskTime.lazy = true;
          if (this.valueContainerTime.length > 0)
            this.$nextTick(() => (this.maskTime.lazy = false));
          break;
      }
    },
    onAccept(evt) {
      const maskRef = evt.detail;

      switch (evt.target.id) {
        case "tel":
          this.valueTel = maskRef.unmaskedValue;
          break;
        case "date":
          this.$nextTick(
            () => (this.valueContainerDate = maskRef.unmaskedValue)
          );
          this.valueDate = maskRef.value;
          break;
        case "time":
          this.$nextTick(
            () => (this.valueContainerTime = maskRef.unmaskedValue)
          );
          this.valueTime = maskRef.value;
          break;
      }
    },
    closeErrorMessage() {
      this.$refs.errorMessage.classList.add("hidden");
    }
  },
  computed: {
    isComplete() {
      return (
        this.valueName != "" &&
        this.valueTel.length == 11 &&
        this.valueDate.length == 5 &&
        this.valueTime.replace("_").length == 5 &&
        this.playerCount >= 1 &&
        this.playerCount <= 5 &&
        this.valuePlatform != ""
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.form-reserve {
  max-width: 500px;
}

.datetime {
  display: flex;
  flex-direction: column;
}

.input {
  width: 100%;
  margin-bottom: 10px;
}

.button {
  margin-top: 20px;
}
</style>