<template>
  <div class="container--vertical">
    <div class="input-group">
      <input type="email" v-model="email" class="input" placeholder="Логин" />
      <input type="password" v-model="password" class="input" placeholder="Пароль" />
      <ui-button class="button button--form" raised @click="login">Войти</ui-button>
    </div>
  </div>
</template>

<script>
import { auth } from "../firebase";

export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      errors: []
    };
  },
  methods: {
    login() {
      auth
        .signInWithEmailAndPassword(this.email, this.password)
        .then(user => {
          if (user) {
            this.$router.push("/admin");
          }
        })
        .catch(error => {
          let errorMessage = error.message;
          alert(errorMessage);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/utils/variables.scss";
@import "@/assets/scss/utils/layout.scss";

.container--vertical {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  flex-grow: 1;

  @media #{$mobile-width} {
    flex-direction: row;
  }
}

.input-group {
  display: flex;
  flex-direction: column;

  @media #{$mobile-width} {
    flex-direction: row;
    align-items: center;
  }

  .button {
    margin-top: 10px;

    @media #{$mobile-width} {
      margin-top: 0;
    }
  }
}
</style>