<template>
  <div class="home">
    <div class="search" :class="{open: isOpen}">
      <div class="container" :class="{collapse: isLikedTab}">
        <div class="search__wrapper" v-show="isOpen">
          <ui-icon class="search__icon" icon="search"></ui-icon>
          <input type="text" class="search__input" v-model="query" placeholder="Найти игру..." />
          <div class="select" v-show="selectedPlatform == 'HTC'">
            <label class="select__label" for="genre">Жанр</label>
            <select v-model="genreHTC" id="genre" class="select__wrapper" @change="selectGenre">
              <option :selected="genreHTC" value="все">все</option>
              <option>симулятор</option>
              <option>шутер</option>
              <option>экшн</option>
              <option>музыкальная</option>
              <option>песочница</option>
              <option>файтинг</option>
              <option>хоррор</option>
              <option>спорт</option>
              <option>квест</option>
              <option>стратегия</option>
              <option>головоломка</option>
              <option>аттракцион</option>
              <option>приключение</option>
              <option>творчество</option>
              <option>многопользовательская</option>
            </select>
          </div>
          <div class="select" v-show="selectedPlatform == 'PSVR'">
            <label class="select__label" for="genre">Жанр</label>
            <select v-model="genrePSVR" id="genre" class="select__wrapper" @change="selectGenre">
              <option :selected="genrePSVR" value="все">все</option>
              <option>симулятор</option>
              <option>экшн</option>
              <option>аркада</option>
              <option>гонка</option>
              <option>хоррор</option>
              <option>квест</option>
              <option>аттракцион</option>
              <option>приключение</option>
              <option>многопользовательская</option>
            </select>
          </div>
          <div class="select" v-show="selectedPlatform == 'PS4'">
            <label class="select__label" for="genre">Жанр</label>
            <select v-model="genrePS4" id="genre" class="select__wrapper" @change="selectGenre">
              <option :selected="genrePS4" value="все">все</option>
              <option>экшн</option>
              <option>ролевая</option>
              <option>музыкальная</option>
              <option>аркада</option>
              <option>гонка</option>
              <option>спорт</option>
              <option>файтинг</option>
              <option>песочница</option>
              <option>симулятор</option>
              <option>интерактивное кино</option>
              <option>многопользовательская</option>
            </select>
          </div>
          <ui-switch v-model="isChild" v-show="selectedPlatform !== 'МОИ'">Для детей</ui-switch>
          <ui-switch v-model="isLocalMultiplayer" v-show="selectedPlatform == 'PS4'">На двоих</ui-switch>
          <!-- <ui-button class="options-button" icon="tune" @click="toggleOptions()"></ui-button> -->
        </div>
        <ui-button
          class="search-button"
          v-if="!isOpen"
          icon="tune"
          @click="showOptions()"
          title="Опции"
        ></ui-button>
        <ui-button class="search-button" v-else icon="close" @click="hideOptions()" title="Закрыть"></ui-button>
      </div>
    </div>
    <div class="container container--nopadding">
      <ui-tabs fullwidth @tab-click="selectTab">
        <ui-tab
          ref="tab"
          :key="tab.title"
          :selected="tab.title === 'HTC'"
          :title="tab.title"
          v-for="tab in platformTabs"
        >
          <!-- <vue-element-loading :active="loading">
            <img :src="'images/pikachu.gif'" width="55px" height="55px"/>
          </vue-element-loading> -->
          <div v-if="isEmpty && isLikedTab" class="empty">
            <ui-icon>inbox</ui-icon>
            <p>
              Ваш список избранных игр пуст.
              <br />Чтобы добавить игру в избранное, на странице игры нажмите на значок звёздочки.
              <br />Чтобы удалить игру из избранного - нажмите на звёздочку ещё раз.
            </p>
          </div>
          <ui-progress-circular :auto-stroke="false" :size="54" v-show="loading"></ui-progress-circular>
          <div
            v-if="isEmptyQuery == 0 && isOpen && !isLikedTab"
          >По вашему запросу ничего не найдено... ¯\_(ツ)_/¯</div>
          <game-card v-for="(game, index) in showGames" :key="index" :game="game"></game-card>
          <back-to-top>
            <button class="fab fab--up" title="Наверх">
              <ui-icon>keyboard_arrow_up</ui-icon>
              <ui-ripple-ink></ui-ripple-ink>
            </button>
          </back-to-top>
        </ui-tab>
      </ui-tabs>
    </div>
  </div>
</template>

<script>
import GameCard from "@/components/GameCard.vue";

export default {
  name: "Home",
  data() {
    return {
      // publicPath: process.env.BASE_URL,
      loading: true,
      isOpen: false,
      query: "",
      isChild: false,
      isLocalMultiplayer: false,
      genreHTC: "все",
      genrePSVR: "все",
      genrePS4: "все",
      tab: null,
      platformTabs: [
        {
          title: "HTC"
        },
        {
          title: "PSVR"
        },
        {
          title: "PS4"
        },
        {
          title: "МОИ"
        }
      ],
      games: [],
      isLikedTab: false,
      selectedPlatform: "HTC",
      selectedGenre: "все"
    };
  },
  components: {
    GameCard
  },
  methods: {
    selectTab: function(evt) {
      if (evt.title == "МОИ") {
        this.isLikedTab = true;
      } else {
        this.isLikedTab = false;
      }
      this.selectedPlatform = evt.title;

      if (this.selectedPlatform == "HTC") {
        this.selectedGenre = this.genreHTC;
      } else if (this.selectedPlatform == "PSVR") {
        this.selectedGenre = this.genrePSVR;
      } else if (this.selectedPlatform == "PS4") {
        this.selectedGenre = this.genrePS4;
      }
    },
    selectGenre(evt) {
      this.selectedGenre = evt.target.value;
    },
    showOptions() {
      this.isOpen = true;
    },
    hideOptions() {
      this.query = "";
      this.isOpen = false;
    },
    childMounted() {
      // this.loading = false
    }
  },
  mounted() {
    this.$store.dispatch("bindGames");
    // this.$nextTick(() => {
    //   this.loading = false;
    // });
    // this.show = false;
    setTimeout(() => (this.loading = false), 3000);
  },
  computed: {
    showGames() {
      if (this.selectedPlatform == "МОИ") {
        return this.$store.getters.wishlist(this.query);
      } else {
        return this.$store.getters.showGamesByPlatform(
          this.selectedPlatform,
          this.selectedGenre,
          this.query,
          this.isChild,
          this.isLocalMultiplayer
        );
      }
    },
    isEmpty() {
      return !this.$store.state.wishlistIds.length;
    },
    isEmptyQuery() {
      return this.$store.getters.showGamesByPlatform(
        this.selectedPlatform,
        this.selectedGenre,
        this.query,
        this.isChild,
        this.isLocalMultiplayer
      ).length;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/utils/variables.scss";
@import "@/assets/scss/utils/layout.scss";

.home {
  min-height: 100vh;
}

.empty {
  margin: 0 auto;
  text-align: center;

  p {
    text-align: center;
  }

  .ui-icon {
    font-size: 15rem;
    color: rgba(255, 255, 255, 0.1);
  }
}

.search {
  margin-bottom: 20px;
  background: $main-red;
  background: $header-gradient;

  &__icon {
    position: absolute;
    top: 8px;
    left: 10px;
    background: transparent;
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
  }

  &.open {
    background: $main-violet;

    .container {
      height: 120px;
      padding-bottom: 10px;

      &.collapse {
        height: 42px;
        padding-bottom: 0;
      }

      @media (min-width: 525px) {
        height: 94px;
      }

      .select {
        width: 100%;
        margin-bottom: 5px;
        padding-left: 16px;

        @media (min-width: 525px) {
          width: auto;
          margin-bottom: 0;
        }

        @media #{$tablet-width} {
          padding-left: 0;
        }
      }
    }

    .search__input {
      margin-bottom: 10px;
    }
  }

  &__input {
    width: 100%;
    height: 42px;
    padding-left: 40px;
    padding-right: 50px;
    border-color: transparent;
    color: $white;
    background: rgba(0, 0, 0, 0.8);
  }

  .container {
    height: 42px;
    position: relative;
    padding: 0;

    .ui-button.search-button,
    .ui-button.options-button {
      position: absolute;
      top: 0;
      width: 42px;
      height: 42px;
      min-width: auto;
      padding: 5px;
      z-index: 10;
      border-radius: 0;
    }

    .ui-button.search-button {
      right: 0;
    }

    .ui-button.options-button {
      right: 52px;
    }
  }
}

::-webkit-input-placeholder {
  color: #fff;
}

::-moz-placeholder {
  color: #fff;
}

:-moz-placeholder {
  color: #fff;
}

:-ms-input-placeholder {
  color: #fff;
}

input:focus {
  outline: none;
}

.search-options {
  margin-top: -20px;
  margin-bottom: 20px;
  background: darken($color: $main-violet, $amount: 5);
}

.ui-progress-circular {
  margin-left: -27px;
}

.velmld-overlay {
  background-color: transparent !important;
}
</style>