<template>
  <div class="container--vertical">
    <router-link tag="a" to="/" title="На главную">
      <ui-button icon="home"></ui-button>
    </router-link>
    <div
      class="game-header"
      :style="{ 'background-image': 'linear-gradient(rgba(33, 8, 50, .1), rgba(33, 8, 50, 1)), url(' + game.image1 + ')' }"
    >
      <h1 class="game-title">{{ game.title }}</h1>
    </div>
    <h2>Об игре</h2>
    <p class="game-desc">{{ game.description }}</p>
    <p>
      <span class="game__genre">Жанр:</span>
      {{ game.genre }}
    </p>
    <p>
      <span v-if="game.tags" class="game__tags">Теги:</span>
      <span class="game-tag" v-for="(tag,index) in game.tags" :key="index">{{ tag.value + " " }}</span>
    </p>
    <div class="swiper-parent">
      <swiper class="swiper gallery-top" :options="swiperOptionTop" ref="swiperTop">
        <swiper-slide class="slide-1" ref="slide">
          <img :src="game.image1" :alt="game.title" />
        </swiper-slide>
        <swiper-slide class="slide-2">
          <img :src="game.image2" :alt="game.title" />
        </swiper-slide>
        <swiper-slide class="slide-3">
          <img :src="game.image3" :alt="game.title" />
        </swiper-slide>
        <swiper-slide class="slide-4 slide-video">
          <div class="frame-parent">
            <iframe
              :src="game.video"
              frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </div>
        </swiper-slide>
        <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
        <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
      </swiper>
      <!-- swiper2 Thumbs -->
      <swiper class="swiper gallery-thumbs" :options="swiperOptionThumbs" ref="swiperThumbs">
        <swiper-slide class="slide-1">
          <img :src="game.image1" :alt="game.title" ref="image" />
        </swiper-slide>
        <swiper-slide class="slide-2">
          <img :src="game.image2" :alt="game.title" />
        </swiper-slide>
        <swiper-slide class="slide-3">
          <img :src="game.image3" :alt="game.title" />
        </swiper-slide>
        <swiper-slide class="slide-4">
          <img src="icons/yt.jpg" alt="Youtube Trailer" />
        </swiper-slide>
      </swiper>
    </div>
    <div v-if="similarGames.length" class="similar">
      <h2>Похожие игры</h2>
      <div class="game__similar-games">
        <game-card
          v-for="(game, index) in similarGames"
          :key="index"
          :game="game"
          v-on:change-game="changeGame($event)"
        ></game-card>
      </div>
    </div>
    <!-- @click.native="showGame(game)" -->
    <button v-if="!isShowFabs" class="fab fab--options" title="Опции" @click="showFabs()">
      <ui-icon>tune</ui-icon>
      <ui-ripple-ink></ui-ripple-ink>
    </button>
    <button v-else class="fab fab--options" title="Закрыть" @click="hideFabs()">
      <ui-icon>close</ui-icon>
      <ui-ripple-ink></ui-ripple-ink>
    </button>
    <button
      v-show="isShowFabs"
      class="fab fab--share"
      ref="modal"
      title="Поделиться"
      @click="shareGame()"
    >
      <ui-icon>share</ui-icon>
      <ui-ripple-ink></ui-ripple-ink>
    </button>
    <button
      v-show="isShowFabs"
      v-if="!liked"
      @click="putLike()"
      :class="{ liked }"
      class="fab fab--fav"
      ref="buttonLike"
      title="Добавить в избранное"
    >
      <ui-icon>star_outline</ui-icon>
      <ui-ripple-ink></ui-ripple-ink>
    </button>
    <button
      v-show="isShowFabs"
      v-else
      @click="putDislike()"
      :class="{ liked }"
      class="fab fab--fav"
      ref="buttonDislike"
      title="Удалить из избранного"
    >
      <ui-icon>star</ui-icon>
      <ui-ripple-ink></ui-ripple-ink>
    </button>
    <ui-alert
      @dismiss="closeAlert"
      type="success"
      removeIcon
      v-show="showAlertAddGame"
    >Игра добавлена в избранное</ui-alert>
    <ui-alert
      @dismiss="closeAlert"
      type="success"
      removeIcon
      v-show="showAlertDeleteGame"
    >Игра удалена из избранного</ui-alert>
    <ui-modal ref="modal" title="Поделись ссылкой на игру">
      <ShareNetwork
        network="vk"
        :url="`https://sergeyprimirenkov.github.io/catalog/#/game/${game.id}`"
        :title="`Поиграл в классную игру в VR-клубе DriV3R - ${game.title}`"
        hashtags="mgnvr,driv3r,виртуальнаяреальность"
      >Поделиться в VK</ShareNetwork>
      <ShareNetwork
        network="facebook"
        :url="`https://sergeyprimirenkov.github.io/catalog/#/game/${game.id}`"
        :title="`Поиграл в классную игру в VR-клубе DriV3R - ${game.title}`"
        hashtags="mgnvr,driv3r,виртуальнаяреальность"
      >Поделиться в Facebook</ShareNetwork>
      <ShareNetwork
        network="twitter"
        :url="`https://sergeyprimirenkov.github.io/catalog/#/game/${game.id}`"
        :title="`Поиграл в классную игру в VR-клубе DriV3R - ${game.title}`"
        hashtags="mgnvr,driv3r,виртуальнаяреальность"
      >Поделиться в Twitter</ShareNetwork>
    </ui-modal>
  </div>
</template>

<script>
import GameCard from "@/components/GameCard.vue";

export default {
  name: "Game",
  data() {
    return {
      game: {},
      isShowFabs: false,
      showAlertAddGame: false,
      showAlertDeleteGame: false,
      swiperOptionTop: {
        loop: true,
        loopedSlides: 4, // looped slides should be the same
        spaceBetween: 5,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        keyboard: {
          enabled: true
        }
      },
      swiperOptionThumbs: {
        loop: true,
        loopedSlides: 4, // looped slides should be the same
        spaceBetween: 5,
        centeredSlides: true,
        slidesPerView: 4,
        touchRatio: 0.2,
        slideToClickedSlide: true
      }
    };
  },
  components: {
    GameCard
  },
  methods: {
    showGame(game) {
      this.game = game;
    },
    closeAlert() {
      this.showAlertAddGame = false;
      this.showAlertDeleteGame = false;
    },
    addToFavorite() {
      this.showAlert = true;
    },
    putLike: function() {
      this.liked = !this.liked;
      this.showAlertAddGame = true;
      setTimeout(() => (this.showAlertAddGame = false), 2000);
    },
    putDislike() {
      this.liked = !this.liked;
      this.showAlertDeleteGame = true;
      setTimeout(() => (this.showAlertDeleteGame = false), 2000);
    },
    shareGame() {
      this.$refs.modal.open();
    },
    showFabs() {
      this.isShowFabs = !this.isShowFabs;
    },
    hideFabs() {
      this.isShowFabs = !this.isShowFabs;
    },
    changeGame(game) {
      this.game = game;
      this.$router.push("/game/" + game.id);
    }
  },
  created() {
    this.game = this.$store.state.games.find(game => {
      return game.id == this.$route.params.id;
    });
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.$swiper;
      const swiperThumbs = this.$refs.swiperThumbs.$swiper;
      swiperTop.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperTop;
    });

    // let slideHeight = this.$refs.image.clientHeight;
    // this.$refs.video.clientHeight = slideHeight;
  },
  computed: {
    similarGames() {
      return this.$store.getters.showSimilarGames(
        this.game.id,
        this.game.genre,
        this.game.tags,
        this.game.platform
      );
    },
    liked: {
      get() {
        return this.$store.state.wishlistIds.includes(this.game.id);
      },
      set(val) {
        this.$store.commit(val ? "addGame" : "removeGame", this.game.id);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/utils/variables.scss";
@import "@/assets/scss/utils/layout.scss";

.container {
  flex-direction: column;
}

img {
  width: 100%;
}

.thumb-example {
  height: 480px;
  background-color: black;
}

.similar {
  margin-top: 40px;
}

.frame-parent {
  position: relative;
  padding: 0 0 56.25% 0;
  height: 0;
  overflow: hidden;
}

.frame-parent iframe,
.frame-parent object,
.frame-parent embed {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.game-header {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  width: 100%;
  height: 300px;
  margin-top: 20px;
  margin-bottom: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 4px;

  h1 {
    margin-bottom: 30px;
  }
}

.game-desc {
  max-width: 700px;
}

.game-tag {
  position: relative;
}

.game-tag:not(:last-child)::after {
  position: absolute;
  right: 0;
  bottom: 0;
  content: ",";
  display: inline;
}

.game-title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>